.style-type {
  --circle-size: 16px;
  --circle-color-light: var(--components-color);
  --circle-color-dark: var(--white-color);
  --input-width: 40px;
  --input-height: 20px;
  --input-bd-size: 1px;
  --input-bd-color: var(--components-color);
  --input-bg-light: var(--white-color);
  --input-bg-dark: var(--components-color);
  align-items: center;
  display: flex;
}

@media screen and (max-width: 1279px) {
  .style-type .style-type__value {
    font-size: 7px;
  }
}

.style-type .style-type__value:first-child {
  margin-right: 14px;
}

.style-type .style-type__value:last-child {
  margin-left: 14px;
}

.style-type .style-type__input[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.style-type .style-type__label {
  cursor: pointer;
  text-indent: -9999px;
  width: var(--input-width);
  height: var(--input-height);
  background: var(--input-bg-light);
  border: var(--input-bd-size) solid var(--input-bd-color);
  border-radius: 100px;
  transition: all .3s;
  display: block;
  position: relative;
}

.style-type .style-type__label:after {
  content: "";
  width: var(--circle-size);
  height: var(--circle-size);
  background: var(--circle-color-light);
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.style-type .style-type__input:checked + .style-type__label {
  background: var(--input-bg-dark);
}

.style-type .style-type__input:checked + .style-type__label:after {
  background: var(--circle-color-dark);
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

